<template>
  <div class="container">
    <!-- Titulo de la pagina en el cuerpo de la pagina -->
    <h1>Colegio</h1>
    <!-- Alerta para la pagina -->
    <b-alert
      :show="dismissCountDown"
      dismissible
      variant="warning"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>
    <!-- Zona en la que se editan los datos del formulario -->
    <form @submit.prevent="editarAlumno(alumnoEditar)" v-if="editar">
      <h3>Editar datos</h3>
      <input type="text" class="form-control my-2" placeholder="RUT" v-model="alumnoEditar.id" />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
        v-model="alumnoEditar.nombre"
      />
      <input type="text" class="form-control my-2" placeholder="Edad" v-model="alumnoEditar.edad" />
      <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
      <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
    </form>
    <!-- Zona en la que se ingresan datos a un formulario -->
    <form @submit.prevent="agregarAlumno" v-if="!editar">
      <h3>Ingreso de datos</h3>
      <input type="text" class="form-control my-2" placeholder="ID" v-model="alumno.id" />
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="alumno.nombre" />
      <input type="text" class="form-control my-2" placeholder="Edad" v-model="alumno.edad" />
      <b-button class="bt-success my-2" type="submit">Agregar</b-button>
    </form>
    <!-- Tabla en la que se visualizan los datos de la tabla -->
    <table class="table">
      <thead>
        <tr>
          <th scope="col">MongoID</th>
          <th scope="col">AlumnoID</th>
          <th scope="col">Nombre</th>
          <th scope="col">Edad</th>
          <th scope="col">Opciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in alumnos" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{item.id}}</td>
          <td>{{item.nombre}}</td>
          <td>{{item.edad}}</td>
          <td>
            <!-- <b-button @click="alerta">Accion</b-button> -->
            <b-button @click="eliminarAlumno(item._id)" class="btn-danger btn-sm mx-2">Eliminar</b-button>
            <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      alumnos: [],
      mensaje: { color: "", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      alumno: { id: "", nombre: "", edad: "" },
      editar: false,
      alumnoEditar: {}
    };
  },
  created() {
    this.listarAlumnos();
  },
  methods: {
    alerta() {
      this.mensaje.color = "success";
      this.mensaje.texto = "Alerta";
      this.showAlert();
    },
    listarAlumnos() {
      this.axios
        .get("/alumno")
        .then(res => {
          console.log(res);
          this.alumnos = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    agregarAlumno() {
      console.log(this.alumno);
      this.axios
        .post("/nuevo-alumno", this.alumno)
        .then(res => {
          this.alumnos.push(res.data);
          this.alumno.id = "";
          this.alumno.nombre = "";
          this.alumno.edad = "";
          this.mensaje.color = "success";
          this.mensaje.texto = "Agregado con exito";
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
          if (this.alumno.id == '') {
            this.mensaje.texto = e.response.data.error.errors.id.message;
          } else if (this.alumno.nombre == '') {
            this.mensaje.texto = e.response.data.error.errors.nombre.message;
          } else {
            this.mensaje.texto = "Error no especificado";
          }
          this.mensaje.color = "danger";
          this.showAlert();
        });
    },
    eliminarAlumno(id) {
      console.log(id);
      this.axios
        .delete(`/alumno/${id}`)
        .then(res => {
          const index = this.alumnos.findIndex(
            item => item._id === res.data._id
          );
          this.alumnos.splice(index, 1);
          this.mensaje.color = "alert";
          this.mensaje.texto = "Fila eliminada";
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    activarEdicion(id) {
      this.editar = true;
      console.log(id);
      this.axios
        .get(`/alumno/${id}`)
        .then(res => {
          this.alumnoEditar = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    editarAlumno(item) {
      this.axios
        .put(`/alumno/${item._id}`, item)
        .then(res => {
          const index = this.alumnos.findIndex(n => n._id === res.data._id);
          this.alumnos[index].id = res.data.id;
          this.alumnos[index].nombre = res.data.nombre;
          this.alumnos[index].edad = res.data.edad;
          this.mensaje.color = "success";
          this.mensaje.texto = "Fila editada";
          this.showAlert();
          this.editar = false;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>